/*
 * Base structure
 */

/* Move down content because we have a fixed navbar that is 50px tall */

body {
	padding-top: 55px;
	padding-bottom: 40px;
	background-color: #b0bec5;
}

.body-block {
	background-color: white;
	-webkit-box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.5);
	-moz-box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.5);
	box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.5);
	-moz-border-radius: 2px;
	-webkit-border-radius: 2px;
	border-radius: 2px;
	border: 0 solid #000;
	padding: 20px;
	margin-top: 10px;
	margin-bottom: 30px;
}


/*
 * Global add-ons
 */

.sub-header {
	padding-bottom: 10px;
	border-bottom: 1px solid #eee;
}


/*
 * Sidebar
 */

/* Hide for mobile, show later */

.sidebar {
	display: none;
}

@media (min-width: 768px) {
	.sidebar {
		position: fixed;
		top: 51px;
		bottom: 0;
		left: 0;
		z-index: 1000;
		display: block;
		padding: 20px;
		overflow-x: hidden;
		overflow-y: auto; /* Scrollable contents if viewport is shorter than content. */
		background-color: #f5f5f5;
		border-right: 1px solid #eee;
	}
}

/* Sidebar navigation */

.nav-sidebar {
	margin-right: -21px; /* 20px padding + 1px border */
	margin-bottom: 20px;
	margin-left: -20px;
}

.nav-sidebar > li > a {
	padding-right: 20px;
	padding-left: 20px;
}

.nav-sidebar > .active > a {
	color: #fff;
	background-color: #428bca;
}

/*
 * Main content
 */

.main {
	padding: 20px;
}

@media (min-width: 768px) {
	.main {
		padding-right: 40px;
		padding-left: 40px;
	}
}

.main .page-header {
	margin-top: 0;
}


/*
 * Placeholder dashboard ideas
 */

.placeholders {
	margin-bottom: 30px;
	text-align: center;
}

.placeholders h4 {
	margin-bottom: 0;
}

.placeholder {
	margin-bottom: 20px;
}

.placeholder img {
	display: inline-block;
	border-radius: 50%;
}

.tdRdoBtn label {
	text-align: center;
	display: block;
}

.custab {
	border: 1px solid #ccc;
	padding: 5px;
	margin: 5% 0;
	box-shadow: 3px 3px 2px #ccc;
	transition: 0.5s;
}

.custab:hover {
	box-shadow: 3px 3px 0 transparent;
	transition: 0.5s;
}

.badge-complete {
	background-color: #468847;
}

.badge-pending {
	background-color: #f89406;
}

.badge-disabled {
	background-color: #b94a48;
}


.select-or {
	position: relative;
	font-size: 18px;
	color: #aaa;
	margin-top: 10px;
	margin-bottom: 10px;
	padding-top: 10px;
	padding-bottom: 10px;
}

.span-or {
	display: block;
	position: absolute;
	left: 50%;
	top: -2px;
	margin-left: -25px;
	background-color: #fff;
	width: 50px;
	text-align: center;
}

.hr-or {
	background-color: #cdcdcd;
	height: 1px;
	margin-top: 0 !important;
	margin-bottom: 0 !important;
}

.hr-question {
	background-color: #cdcdcd;
	height: 1px;
	margin-top: 5px !important;
	margin-bottom: 5px !important;
}

.footer {
	position: fixed;
	padding: 4px;
	bottom: 0;
	right: 0;
	margin: 0;
	min-height: 0;
	color: #7f7f7f;
	background-color: lightgrey;
	font-size: 20px;
	line-height: 40px;
}

.ctr-contents {
	text-align: center;
}

.form-block:nth-child(odd) {
	background-color: #eaeaea;
}

.form-instruction-block .row div {
	margin-top: 20px;
	margin-bottom: 20px;
}

.row {
	margin-left: 0;
	margin-right: 0;
}

.print {
	display: none;
}

.pointer {
	cursor: pointer;
}

.view-evaluation,
.view-idless-evaluation {
	cursor: pointer;
}

.table > tbody > tr:hover {
	background-color: #f5f5f5;
}

.question {
	margin-bottom: 50px;
	page-break-inside: avoid;
}

.question-option {
	padding: 10px;
	margin-bottom: 20px;
}

.flexbox .question-option {
	display: flex;
	flex-direction: column;
	flex: 1 1 auto;
}

.no-flexbox .question-option {
	display: table-cell;
}

.no-flexbox .question-option textarea {
	width: 100%;
}

.flexbox.flexwrap .question-body {
	display: flex;
	flex-flow: row wrap;
	justify-content: space-around;
}

.no-flexbox .question-body {
	display: table;
	width: 100%;
}

.no-flexbox .question.expanded-descriptions .question-option {
	display: block;
}

@media (max-width: 768px) {
	.question.expanded-descriptions .question-option {
		width: 100%;
	}
}

@media (min-width: 768px) {
	.question.expanded-descriptions .question-option {
		max-width: 40%;
	}
}

@media (min-width: 992px) {
	.question.expanded-descriptions .question-option {
		max-width: 30%;
	}
}

@media (min-width: 1200px) {
	.question.expanded-descriptions .question-option {
		max-width: 25%;
	}
}

.question-option label {
	text-align: center;
	display: block;
}


.question-footer {
	text-align: center;
}

.question-description-toggle button.btn {
	margin: 0 10px;
}

.instruction {
	margin: 40px;
}

.instruction p {
	margin-bottom: 30px;
}

.select2-results__options--nested > .select2-results__option {
	padding-left: 24px !important;
}

.select2-results__option {
	padding-top: 4px !important;
	padding-bottom: 4px !important;
}

.select2-results__group {
	color: #222 !important;
	font-size: 16px !important;
}

.select2-container--bootstrap .select2-results > .select2-results__options {
	max-height: 400px;
}

.ms-container {
	margin: auto;
}

.ms-container .ms-optgroup-label {
	font-weight: bold;
	font-size: large;
	color: #555;
	padding: 5px;
}

@media (max-width: 768px) {
	.paginate_button.disabled {
		display: none;
	}
}

@media print {
	.print {
		display: block;
		overflow: visible;
		white-space: pre;
		white-space: pre-wrap;
	}

	.noprint {
		display: none;
	}

	table {
		width: 100%;
	}

	.question-footer {
		display: none;
	}
}

.center-block {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.table-date-cell,
.table-date-time-cell,
.table-date-range-cell {
	cursor: help;
}

.containing-label {
	width: 100%;
	margin-bottom: 0;
}

.containing-label input,
.containing-label textarea,
.containing-label select,
.containing-label .select2,
.containing-label .input-group,
.containing-label button {
	margin-top: 5px;
	font-weight: normal;
}

.labelless-button,
.labelless-element {
	margin-top: 25px;
	text-align: center;
}

.form-group .select2.select2-container {
	width: 100% !important;
}

.input-group .select2.select2-container.select2-container--bootstrap .select2-selection {
	border-top-right-radius: 0;
	border-bottom-right-radius: 0;
}

.input-group.no-right-addons .select2.select2-container.select2-container--bootstrap .select2-selection {
	border-top-right-radius: 4px;
	border-bottom-right-radius: 4px;
}

.input-group-addon + .input-group .select2.select2-containerselect2-container--bootstrap .select2-selection {
	border-top-left-radius: 0;
	border-bottom-left-radius: 0;
}

.row > .checkbox {
	margin-top: -5px;
}

.appear-not-readonly {
	background-color: transparent !important;
}

.alert {
	margin: 20px 0;
}

.btn-lg-submit-container {
	margin-top: 40px;
	text-align: center;
}

label.normal-text-label {
	font-weight: normal;
}

.component-list-item {
	border-bottom: 1px solid rgba(0, 0, 0, 0.25);
	padding: 5px 0;
}

.component-list-item:nth-child(even) {
	background-color: rgba(0, 0, 0, 0.05);
}

.component-list-item small {
	font-size: 0.75em;
	color: rgba(0, 0, 0, 0.55);
	display: block;
}

/* stylelint-disable-next-line */
select-two {
	overflow: hidden;
}

[v-cloak] {
	display: none;
}

[v-visible],
[v-visible\.once] {
	visibility: hidden;
}

.tooltip {
	transition: opacity 0.15s;
}

.tooltip[aria-hidden="false"] {
	visibility: visible;
	opacity: 1;
}

/* Don't let <input type="hidden" /> break first .input-group control */

.input-group input[type="hidden"]:first-child + .form-control {
	border-top-left-radius: 4px;
	border-bottom-left-radius: 4px;
}

.is-required .help-block,
.is-required .control-label,
.is-required .radio,
.is-required .checkbox,
.is-required .radio-inline,
.is-required .checkbox-inline,
.is-required.radio label,
.is-required.checkbox label,
.is-required.radio-inline label,
.is-required.checkbox-inline label {
	color: #337ab7;
}

.is-required .form-control {
	border-color: #337ab7;
	-webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
	box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
}
